import React from 'react';
import ReactDOM from 'react-dom';
import BusSchedule from './BusSchedule'
import registerServiceWorker from './registerServiceWorker';

import './index.css'


fetch('./shelters.json')
  .then((response) => response.json())
  .then((responseJson) => {
    const date = new Date();

    let hours = date.getHours().toString();
    let minute = date.getMinutes() < 10 ? '0' + date.getMinutes().toString() : date.getMinutes().toString();
    let day = date.getDay();


    let concatHoursAndMinute = hours + minute;
    let concatHoursAndMinuteInt = parseInt( concatHoursAndMinute );
    // let concatHoursAndMinuteInt = 1801;

    for (let i = 0; i < responseJson.length; i++) {

      let buss = responseJson[i].routes;


      function getNearestBus( _day ) {
        for (let b = 0; b < buss.length; b++) {

          let _shelterBus = buss[b].route_schedule[_day];


          if ( _shelterBus ) {
            if ( _day === '' && day !== 6 ) {
                return
            };
            const shelterBus = _shelterBus;

            for (let t = 0; t < shelterBus.length; t++) {

              let _time = shelterBus[t];

              let time = parseInt(_time.substring(0,5).replace(/[^0-9]/gim,''));
              // let time = 2000;


              if ( time >= concatHoursAndMinuteInt ) {

                let minute;

                let hour_difference = parseInt(time/100) - parseInt(concatHoursAndMinuteInt/100);
                let hour = parseInt((time - concatHoursAndMinuteInt)/100);

                if ( time - concatHoursAndMinuteInt <= 60 && hour_difference === 0) {
                  minute = time - concatHoursAndMinuteInt;
                } else if ( ((time - concatHoursAndMinuteInt) % 100) > 40 ) {
                  minute = (time - concatHoursAndMinuteInt) % 100 - 40
                } else {
                  minute = ((time - concatHoursAndMinuteInt) % 100);
                }

                buss[b][`next`] = `${hour}:${minute > 9 ? minute : '0' + minute}`;
                break
              }
            }
          }
        }
      }


      if ( day === 0 || day === 6 ){
        getNearestBus( '' );
        getNearestBus( '' );
      } else {
        getNearestBus( '' );
      }
    }

    ReactDOM.render(<BusSchedule shelters={responseJson}/>, document.getElementById('root'));
  })
  .catch((error) => {
    console.error(error);
  });


registerServiceWorker();



